html,body, #root{

  background-image: url(./Assests/Images/coupon.jpg) ;
  background-size: 100% ;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  text-align: center;
  font-style: bold;



  /* background-image: linear-gradient(45deg,#61dafb,#c0e7f1); */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Box {
  display: block;
  text-align: center;
  border: 1px solid black;
  background-color: cornsilk;
  color: peru;
  padding: 5px;
  margin: 5px;
  border-radius: 10px 10px;
  box-shadow: 2px;
}

.Box2 {
  display: inline-block;
  text-align: center;
  border: 1px solid black;
  background-color: cornsilk;
  color: peru;
  padding: 5px;
  margin: 5px;
  border-radius: 10px 10px;
  box-shadow: 2px;
}


