.myGrid {

    /* background-color: #85FFBD; */
/* background-image: linear-gradient(45deg, #85FFBD 0%, #FFFB7D 100%); */

    width: 100%;
    height: 100%;
    display: grid;
    grid: 
        "header header header" 10%
	"leftSidebar main rightSidebar" 80%
	"footer footer footer" 10%
        / 10% 80% 10%;
    grid-gap: 0px;
    grid-auto-flow: row dense;

    
    }


.header {
    background: #3f8c79ee;
    grid-area: header;
}
.leftSidebar {
    /* border-right: 1px solid #03256C; */
    grid-area: leftSidebar;
}
.main {
        
    overflow: auto;
    overflow-y:none;
    justify-content:center;
    align-items:center;
    padding-top: 10%;
    grid-area: main;
  
}
 .rightSidebar {
    /* border-left: 3px solid #03256C; */
    grid-area: rightSidebar;
    
}
.footer {
    background:#80a0e5;
    border-top: 3px solid #03256C;
    grid-area: footer;
}




